import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import LinearProgress from '@material-ui/core/LinearProgress';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import StopIcon from '@material-ui/icons/Stop';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import PauseIcon from '@material-ui/icons/Pause';
import SkipNextIcon from '@material-ui/icons/SkipNext';
import LoopIcon from '@material-ui/icons/Loop';
import ShuffleIcon from '@material-ui/icons/Shuffle';
import Tooltip from '@material-ui/core/Tooltip';
import VolumeUpIcon from '@material-ui/icons/VolumeUp';
import VolumeDownIcon from '@material-ui/icons/VolumeDown';
import Box from '@material-ui/core/Box';
import ReplayIcon from '@material-ui/icons/Replay';
import styled from 'styled-components';


const ThemedCard = styled(Card)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
    display: block;
    width: 500px;
`;
const ThemedIconButton = styled(IconButton)`
    color: ${({ theme }) => theme.text};
`;
const DetailsDiv = styled.div`
    display: block;
    text-align: center;
`;
const ControlsDiv = styled.div`
    display: flex;
    justify-content: center;
    paddingLeft: 1em;
    paddingBottom: 1em;
`;
const ThemedCardMedia = styled(CardMedia)`
    
`;
const ThemedCardContent = styled(CardContent)`
    flex: 1 0 auto;
`;
const LoopIconActive = styled(LoopIcon)`
    color: ${({ theme }) => theme.accent_secondary};
`;
const LoopIconInactive = styled(LoopIcon)`
    color: ${({ theme }) => theme.text};
`;

export default class AudioCurrentTrack extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            audio_track: {},
            img_src: "",
            track_hash: "",
            status: "",
            volume: 0,
            looping: false,
            duration: 0,
            progress: 0,
            progress_string : "",
            duration_string : ""
        };
    }

    componentDidMount() {
        if (Object.keys(this.props.socketData).length > 0) {
            if (this.props.socketData.audio_data) {
                this.setState({
                    audio_track: this.props.socketData.audio_data.track,
                    volume: this.props.socketData.audio_data.volume,
                    status: this.props.socketData.audio_data.status,
                    looping: this.props.socketData.audio_data.loop,
                    img_src: this.props.socketData.audio_data.img_uri_formatted,
                    track_hash: this.props.socketData.audio_data.hash,
                    duration: this.props.socketData.audio_data.duration_time,
                    progress: this.props.socketData.audio_data.progress_time,
                    progress_string: this.props.socketData.audio_data.progress_string,
                    duration_string: this.props.socketData.audio_data.duration_string
                });
            }
        }
    }

    stopTrack = () => {
        fetch(`/api/audio/stop`, {
            method: 'POST'
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    nextTrack = () => {
        fetch(`/api/audio/nexttrack`, {
            method: 'POST'
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    togglePause = () => {
        if (this.state.status === "Paused") { 
            fetch(`/api/audio/resume`, {
                method: 'POST'
            })
            .then(res => (res.ok ? res : Promise.reject(res)))
            .then(res => res.json())
            .then(json_data => {
                console.log(json_data.status);
            })
            .catch(e => console.log(e));
        }
        else if (this.state.status === "Playing") {
            fetch(`/api/audio/pause`, {
                method: 'POST'
            })
            .then(res => (res.ok ? res : Promise.reject(res)))
            .then(res => res.json())
            .then(json_data => {
                console.log(json_data.status);
            })
            .catch(e => console.log(e));
        }
    }

    replayTrack = () => {
        fetch(`/api/audio/replay`, {
            method: 'POST'
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    toggleLoop = () => {
        fetch(`/api/audio/loop`, {
            method: 'POST'
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    shuffleQueue = () => {
        fetch(`/api/audio/shuffle`, {
            method: 'POST'
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    increaseVolume = () => {
        fetch(`/api/audio/increasevolume`, {
            method: 'POST'
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    decreaseVolume = () => {
        fetch(`/api/audio/decreasevolume`, {
            method: 'POST'
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    componentDidUpdate(prev_props, prev_state) {
        if (Object.keys(this.props.socketData).length > 0) {
            if (prev_props.socketData.audio_data.track) {
                if (this.state.track_hash !== this.props.socketData.audio_data.hash) {
                    this.setState({
                        audio_track: this.props.socketData.audio_data.track,
                        status: this.props.socketData.audio_data.status,
                        looping: this.props.socketData.audio_data.loop,
                        volume: this.props.socketData.audio_data.volume,
                        img_src: this.props.socketData.audio_data.img_uri_formatted,
                        track_hash: this.props.socketData.audio_data.hash,
                        duration: this.props.socketData.audio_data.duration_time,
                        progress: this.props.socketData.audio_data.progress_time,
                        progress_string: this.props.socketData.audio_data.progress_string,
                        duration_string: this.props.socketData.audio_data.duration_string
                    });
                }
            }
        }
    }

    render() {
        return (
        <ThemedCard>
            <DetailsDiv>
                    <ThemedCardContent>
                        <Typography component="h5" variant="h5" noWrap={true}>
                            {this.state.audio_track.name || "No Track Available"}
                        </Typography>
                        <Typography component="h5" variant="subtitle1" noWrap={true}>
                            {this.state.status === "Paused" ? "[PAUSED]" : ""}
                        </Typography>
                    </ThemedCardContent>
                    <ControlsDiv>
                        <Tooltip title="Stop and Clear Queue" aria-label="stop-tooltip">
                            <ThemedIconButton aria-label="stop" onClick={this.stopTrack}>
                                <StopIcon/>
                            </ThemedIconButton>
                        </Tooltip>
                        <Tooltip title={`${this.state.status === "Paused" ? "Play" : "Pause"}`} aria-label="play-tooltip">
                            <ThemedIconButton aria-label="play/pause" onClick={this.togglePause}>
                                {
                                    this.state.status === "Paused" ? <PlayArrowIcon/> : <PauseIcon/>
                                }
                            </ThemedIconButton>
                        </Tooltip>
                        <Tooltip title="Next Track" aria-label="next-tooltip">
                            <ThemedIconButton aria-label="next" onClick={this.nextTrack}>
                                <SkipNextIcon />
                            </ThemedIconButton>
                        </Tooltip>
                        <Tooltip title={`${this.state.looping ? "Disable" : "Enable"} Looping`} arial-label="loop-tooltip">
                            <ThemedIconButton aria-label="loop" onClick={this.toggleLoop}>
                                {
                                    this.state.looping ?
                                    <LoopIconActive/> : <LoopIconInactive/>
                                }
                            </ThemedIconButton>
                        </Tooltip>    
                        <Tooltip title="Replay Track" aria-label="replay-tooltip">
                            <ThemedIconButton aria-label="replay" onClick={this.replayTrack}>
                                <ReplayIcon/>
                            </ThemedIconButton>
                        </Tooltip>
                        <Tooltip title="Shuffle Queue" aria-label="shuffle-tooltip">
                            <ThemedIconButton aria-label="shuffle" onClick={this.shuffleQueue}>
                                <ShuffleIcon/>
                            </ThemedIconButton>
                        </Tooltip>
                    </ControlsDiv>
                    <DetailsDiv>
                        <Typography variant="subtitle2">Volume: {this.state.volume}</Typography>
                        <ControlsDiv>
                        <Tooltip title="Decrease Volume" aria-label="volume-down-tooltip">
                            <ThemedIconButton aria-label="volume-down" onClick={this.decreaseVolume}>
                                <VolumeDownIcon/>
                            </ThemedIconButton>
                        </Tooltip>
                        <Tooltip title="Increase Volume" aria-label="volume-up-tooltip">
                            <ThemedIconButton aria-label="volume-up" onClick={this.increaseVolume}>
                                <VolumeUpIcon/>
                            </ThemedIconButton>
                        </Tooltip>
                        </ControlsDiv>
                    </DetailsDiv>
            </DetailsDiv>
            <ThemedCardMedia
                style = {{height: 0, paddingTop: '56%'}}
                image={this.state.img_src || "..."}
            />
            <DetailsDiv display="flex" alignItems="center">
                <Box minWidth={35}>
                    <Typography variant="body2">{this.state.duration >= 0 ? `${this.state.progress_string} - ${this.state.duration_string}` : ''}</Typography>
                </Box>
                <Tooltip title={this.state.duration >= 0 ? `${this.state.progress_string} - ${this.state.duration_string}` : "Progress Bar"} aria-label="progress-bar-tooltip">
                    <Box width="100%" mr={1}>
                        <LinearProgress variant="determinate" color="secondary" value={this.state.duration >= 0 ? `${this.state.progress/this.state.duration * 100}` : 0}/>
                    </Box>
                </Tooltip>
            </DetailsDiv>
        </ThemedCard>
        );
    }
}