import React from 'react';
import ReactJson from 'react-json-view';
import styled from 'styled-components';
import PluginMetadataForm from './PluginMetadataForm';
import {Paper, Typography} from '@material-ui/core';


const ThemedPaper = styled(Paper)`
  background-color: ${({ theme }) => theme.contrast};
  color: ${({ theme }) => theme.text};
  padding: 15px;
`;
const ThemedPaperInner = styled(Paper)`
  background-color: ${({ theme }) => theme.body};
  color: ${({ theme }) => theme.text};
  padding: 15px;
`;

export default class PluginMetadata extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            metadata: {}
        }
    }

    getPluginMetadata = (plugin_name) => {
        fetch(`/api/admin/plugins/metadata/get`, {
            method: 'POST',
            body: JSON.stringify({"text": `${plugin_name}`})
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
            this.setState({metadata: json_data.data.metadata});
        })
        .catch(e => console.log(e));
    }

    componentDidMount() {
        this.getPluginMetadata(this.props.plugin_name);
    }

    render() {
        return(
            <ThemedPaper key={`plugin-metadata-${this.props.plugin_name}`}>
                <ThemedPaperInner elevation={5}>
                    <PluginMetadataForm plugin_name={this.props.plugin_name}/>
                </ThemedPaperInner>
                <br/>
                <ThemedPaperInner elevation={5}>
                    <Typography>Metadata File Preview</Typography><br/>
                    <ReactJson key={`json-viewer-${this.props.plugin_name}`} src={this.state.metadata} iconStyle="square" collapsed={1} displayDataTypes={false}
                        theme={this.props.theme}/>
                </ThemedPaperInner>
            </ThemedPaper>
        );
    }

}