import React, {Fragment} from 'react';
import {List, ListItem, ListItemIcon, Paper, ListItemText, ListItemSecondaryAction, Typography, Grid, Divider} from '@material-ui/core';
import AudioTrackItem from './AudioTrackItem';
import styled from 'styled-components';
import AudioCurrentTrack from './AudioCurrentTrack';

const ThemedList = styled(List)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
`;
const ThemedTypography = styled(Typography)`
    color: ${({ theme }) => theme.text};
`;

export default class AudioTrackList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            list_hash: "",
            track_list: []
        }
    }

    componentDidMount() {
        if (Object.keys(this.props.socketData).length > 0) {
            if (this.props.socketData.audio_data.queue) {
                this.setState({
                    track_list: this.props.socketData.audio_data.queue,
                    list_hash: this.props.socketData.audio_data.hash,
                });
            }
        }
    }

    componentDidUpdate(prev_props, prev_state) {
        if (Object.keys(this.props.socketData).length > 0) {
            if (prev_props.socketData.audio_data) {
                if (this.state.list_hash !== this.props.socketData.audio_data.hash) {
                    this.setState({
                        track_list: this.props.socketData.audio_data.queue,
                        list_hash: this.props.socketData.audio_data.hash,
                    });
                }
            }
        }
    }

    render() {
        return (
            <Grid item xs={12}>
                <ThemedList component={Paper}>
                    <ListItem style={{display: 'flex', justifyContent: 'center'}}>
                        <AudioCurrentTrack socketData={this.props.socketData}/>
                    </ListItem>
                    <ListItem>
                        <ListItemIcon>
                            <ThemedTypography noWrap={true}><b>#</b></ThemedTypography>
                        </ListItemIcon>      
                        <ListItemText
                            primary={<ThemedTypography variant="body1"><b>Track Name</b></ThemedTypography>}
                            secondary={<ThemedTypography variant="caption"><b>Track URL</b></ThemedTypography>}
                        />
                        <ListItemSecondaryAction>
                            <ThemedTypography><b>Actions</b></ThemedTypography>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <Divider/>
                    {
                        this.state.track_list.map((track, track_index) => (
                            <Fragment>
                                <AudioTrackItem indexText={track_index+""} primaryText={track.name} secondaryText={track.alt_uri} useButtons={true} useIcon={true}/>
                                {this.props.useDividers ? <Divider /> : ""}
                            </Fragment>
                        ))
                    }
                </ThemedList>
            </Grid>
        );
    }
}