import React from 'react';
import styled from 'styled-components';
import {TextField, Button, Typography, Collapse, Paper} from '@material-ui/core';
import {Alert} from '@material-ui/lab';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';


const ThemedTextField = styled(TextField)`
    & label {
        color: ${({ theme }) => theme.text};
    }
    & input {
        color: ${({ theme }) => theme.text};
    }
`;
const ThemedDialog = styled(Paper)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
    user-select: none;
`;
const ThemedAlert = styled(Alert)`
    background-color: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
`;
const ThemedSubmitButton = styled(Button)`
    background-color: ${({ theme }) => theme.contrast2};
    color: ${({ theme }) => theme.text};
    &:hover {
        background-color: ${({ theme }) => theme.accent_secondary};
        color: ${({ theme }) => theme.white};
    }
`;

export default class PluginMetadataForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            metadata: {},
            showAlert: false,
            alertMsg: "",
            alertSeverity: "",
            dialogOpen: false,
            dialogMsg: "",
            dialogAdditionalMsg: ""
        }
    }

    componentDidMount() {
        this.getPluginMetadata(this.props.plugin_name, false);
    }

    handleDialogOpen = (e, pluginName, additionalMsg) => {
        e.preventDefault();
        this.setState({
            dialogOpen: true,
            dialogMsg: `Are you sure you want to update the ${pluginName} metadata file?`,
            dialogAdditionalMsg: additionalMsg
        });
    }

    handleDialogCancel = () => {
        this.setState({
            dialogOpen: false, 
            dialogMsg: "",
            dialogAdditionalMsg: ""
        });
    }

    getPluginMetadata = (plugin_name) => {
        fetch(`/api/admin/plugins/metadata/get`, {
            method: 'POST',
            body: JSON.stringify({"text": `${plugin_name}`})
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
            this.setState({metadata: json_data.data.metadata});
        })
        .catch(e => console.log(e));
    }

    setPluginMetadata = (plugin_name, metadata) => {
        fetch(`/api/admin/plugins/metadata/set`, {
            method: 'POST',
            body: JSON.stringify({"name": plugin_name, "text": this.state.metadata})
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
            if (json_data.status === 200) {
                this.setState({showAlert: true, alertMsg: "Successfully updated plugin metadata file!", alertSeverity: "success"});
            }
            else {
                this.setState({showAlert: true, alertMsg: "Encounted an error updating the plugin metadata file!", alertSeverity: "error"});
            }
        })
        .catch(e => {
            console.log(e);
            this.setState({showAlert: true, alertMsg: "Error retrieving the plugins list!", alertSeverity: "error"});
        });
    }

    closeAlert = () => {
        this.setState({showAlert: false, alertMsg: "", alertSeverity: ""});
    }

    updateMetaInfo = (e, key) => {
        const new_meta = this.state.metadata;
        new_meta["Plugin Settings"][key] = e.target.value;
        this.setState({metadata: new_meta});
    }

    updateMetadataFile = (e) => {
        e.preventDefault();
        this.closeAlert();
        this.handleDialogCancel();
        this.setPluginMetadata(this.props.plugin_name);
        this.getPluginMetadata(this.props.plugin_name);
    }

    render() {
        let alert = this.state.showAlert ? <ThemedAlert onClose={this.closeAlert} severity={this.state.alertSeverity}>{this.state.alertMsg}</ThemedAlert> : "";
        return(
            <div>
                <Dialog PaperComponent={ThemedDialog}
                    open={this.state.dialogOpen}
                    onClose={this.handleDialogCancel}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title">{this.state.dialogMsg}</DialogTitle>
                    <DialogContent>
                        {this.state.dialogAdditionalMsg}
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleDialogCancel} color="primary">
                            No
                        </Button>
                        <Button onClick={this.updateMetadataFile} color="secondary">
                            Yes
                        </Button>
                    </DialogActions>
                </Dialog>

                <form key={`plugin-metadata-form-${this.props.plugin_name}`} 
                onSubmit={(e) => this.handleDialogOpen(e, this.props.plugin_name, this.props.plugin_name === "web_server" ? "Updating the web server metadata will require you to manually start the web server again using the <startwebserver> command in the bot." : "Updating the metadata will restart the plugin. Press 'yes' to continue.")} 
                autoComplete="off">
                    <Collapse in={this.state.showAlert}>
                        {alert}
                    </Collapse>
                    <Typography>Plugin Settings - Metadata File</Typography>
                    <br/>
                    {
                        Object.keys(this.state.metadata).length > 0 && "Plugin Settings" in this.state.metadata ?
                        Object.keys(this.state.metadata["Plugin Settings"]).map((inner_key, inner_index) =>
                            <div>
                                <ThemedTextField 
                                    id={`form-item-${inner_key}-${inner_index}`} 
                                    onChange={(e) => this.updateMetaInfo(e, inner_key)}
                                    placeholder="..."
                                    defaultValue={this.state.metadata["Plugin Settings"][inner_key]}
                                    type="text"
                                    label={<Typography>{inner_key}</Typography>}
                                    size="small"
                                    fullWidth={true}
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                />
                            </div>
                        ) : ""
                    }
                    <br/>
                    <ThemedSubmitButton type="submit"><Typography variant="subtitle2">Update Settings</Typography></ThemedSubmitButton>
                </form>
            </div>
        );
    }

}