import React, { Fragment } from 'react';
import SoundBoardItem from './SoundBoardItem';
import Typography from '@material-ui/core/Typography';
import styled from 'styled-components';
import CardActionArea from '@material-ui/core/CardActionArea';
import Tooltip from '@material-ui/core/Tooltip';
import Card from '@material-ui/core/Card';
import Paper from '@material-ui/core/Paper';


const ThemedTypography = styled(Typography)`
    color: ${({ theme }) => theme.text};
`;
const ThemedCard = styled(Card)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
    display: inline-block;
    text-align: center;
    width: 200px;
    height: 50px;
`;
const ThemedActionArea = styled(CardActionArea)`
    height: 100%;
    width: 100%;
`;

export default class SoundBoardList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            clips: []
        }
    }

    getSBClips = () => {
        fetch(`/api/sound_board/clips`, {
            method: 'GET'
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            this.setState({clips: json_data.data.clips})
        })
        .catch(e => console.log(e));
    }

    playRandomClip = () => {
        fetch(`/api/sound_board/random`, {
            method: 'POST'
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    componentDidMount() {
        this.getSBClips();
    }

    render() {
        let lastClipSection = "Misc";
        return (
            <div>
                {
                    <Fragment>
                        <ThemedCard component={Paper}>
                            <Tooltip title={"Play Random Clip"}>
                                <ThemedActionArea onClick={this.playRandomClip}>
                                    <Typography variant="body1" noWrap={true}>{"Random Clip"}</Typography>
                                </ThemedActionArea>
                            </Tooltip>
                        </ThemedCard>
                        {
                            this.state.clips.map((item, index) => (
                            <Fragment key={`frag-${item}-${index}`}>
                                {
                                    item.charAt(0).toLowerCase() !== lastClipSection.charAt(0).toLowerCase() ? <div>
                                        <br/>
                                        {
                                            <ThemedTypography variant="h5">{lastClipSection = item.charAt(0).toLowerCase() !== item.charAt(0).toUpperCase() ? item.charAt(0).toUpperCase() : "Misc"}</ThemedTypography>
                                        }
                                        <hr/>
                                    </div>: ""
                                }
                            
                                <SoundBoardItem clipName={item} key={`${item}-${index}`}/>
                            </Fragment>
                            ))
                        }
                    </Fragment>
                }
            </div>
        );
    }

}