import React from 'react';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';
import styled from 'styled-components';
import Paper from '@material-ui/core/Paper';


const ThemedCard = styled(Card)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
    display: inline-block;
    text-align: center;
    width: 200px;
    height: 50px;
`;

const ThemedActionArea = styled(CardActionArea)`
    height: 100%;
    width: 100%;
`;

export default class SoundBoardItem extends React.Component {

    playClip = () => {
        fetch(`/api/sound_board/play`, {
            method: 'POST',
            body: JSON.stringify({"text": `${this.props.clipName}`})
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    render() {
        return (
            <ThemedCard component={Paper}>
                <Tooltip title={this.props.clipName}>
                    <ThemedActionArea onClick={this.playClip}>
                        <Typography variant="body1" noWrap={true}>{this.props.clipName}</Typography>
                    </ThemedActionArea>
                </Tooltip>
            </ThemedCard>
        );
    }
}

