import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import MusicNoteRoundedIcon from '@material-ui/icons/MusicNoteRounded';
import DeleteRoundedIcon from '@material-ui/icons/DeleteRounded';
import SkipNextRoundedIcon from '@material-ui/icons/SkipNextRounded';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
import styled from 'styled-components';


const ThemedDeleteIcon = styled(DeleteRoundedIcon)`
    color: ${({ theme }) => theme.accent_secondary};
`;
const ThemedSkipIcon = styled(SkipNextRoundedIcon)`
    color: ${({ theme }) => theme.text};
`;
const ThemedMusicIcon = styled(MusicNoteRoundedIcon)`
    color: ${({ theme }) => theme.text};
`;
const ThemedTypography = styled(Typography)`
    color: ${({ theme }) => theme.text};
`;

export default class AudioTrackItem extends React.Component {
    removeTrack = () => {
        fetch(`/api/audio/removetrack`, {
            method: 'POST',
            body: JSON.stringify({"text": `${this.props.indexText}`})
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    skipToTrack = () => {
        fetch(`/api/audio/skipto`, {
            method: 'POST',
            body: JSON.stringify({"text": `${this.props.indexText}`})
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    render() {
        return (
            <ListItem>
                {
                    this.props.useIcon ? 
                    <ListItemIcon>
                        <ThemedMusicIcon/>
                        {this.props.indexText ? <ThemedTypography noWrap={true}><b>{this.props.indexText}</b></ThemedTypography> : ""}
                    </ListItemIcon> : ""
                }
                <ListItemText
                    primary={this.props.primaryText ? <ThemedTypography variant="body1" noWrap={true}>{this.props.primaryText}</ThemedTypography> : 'N/A'}
                    secondary={this.props.secondaryText ? <ThemedTypography variant="caption" noWrap={true}><a href={`${this.props.secondaryText}`} target="_blank" rel="noopener noreferrer">{this.props.secondaryText}</a></ThemedTypography> : null}
                />                
                {
                    this.props.useButtons ?
                    <ListItemSecondaryAction>
                        <Tooltip title="Remove Track" aria-label="delete-track-tooltip">
                            <IconButton edge="end" aria-label="delete-track" onClick={this.removeTrack}>
                                <ThemedDeleteIcon />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title="Skip To Track" aria-label="skip-track-tooltip">
                            <IconButton edge="end" aria-label="skip-track" onClick={this.skipToTrack}>
                                <ThemedSkipIcon />
                            </IconButton>
                        </Tooltip>
                    </ListItemSecondaryAction> : ""            
                }
            </ListItem>
        );
    }
}