import React from 'react';
import styled from "styled-components"
import IconButton from '@material-ui/core/IconButton';
import LoopIcon from '@material-ui/icons/Loop';
import {Alert} from '@material-ui/lab';
import {Typography, Paper, Collapse, Grid} from '@material-ui/core';
import TreeView from '@material-ui/lab/TreeView';
import TreeItem from '@material-ui/lab/TreeItem';
import PluginMetadata from './PluginMetadata';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ExpandMoreRoundedIcon from '@material-ui/icons/ExpandMoreRounded';


const RefreshButton = styled(IconButton)`
    background-color: rgb(0,0,0,0);
    color: ${({ theme }) => theme.text};
    &:hover {
        background-color: ${({ theme }) => theme.accent_secondary};
        color: ${({ theme }) => theme.white};
    }
`;
const ThemedPaper = styled(Paper)`
  background-color: ${({ theme }) => theme.contrast2};
  color: ${({ theme }) => theme.text};
  padding:5px;
`;
const ThemedAlert = styled(Alert)`
    background-color: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
`;

const ThemedTreeView = styled(TreeView)`
    background-color: ${({ theme }) => theme.contrast2};
    color: ${({ theme }) => theme.text};
`;
const ThemedTreeItem = styled(TreeItem)`
    color: ${({ theme }) => theme.text};
    padding: 5px;
`;

class PluginsList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showAlert: false,
            alertMsg: "",
            alertSeverity: "",
            items: []
        }
    }

    refreshPlugins = (useAlert) => {
        fetch('/api/plugins', {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
              }
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            this.setState({items: json_data.data.plugins});
            if (useAlert) {
                this.setState({showAlert: true, alertMsg: "Successfully refreshed the plugins list!", alertSeverity: "success"});
            }
        }).catch(e => {
            console.log(e);
            this.setState({showAlert: true, alertMsg: "Error retrieving the plugins list!", alertSeverity: "error"});
        });
    };

    closeAlert = () => {
        this.setState({showAlert: false, alertMsg: "", alertSeverity: ""});
    }

    componentDidMount() {
        this.refreshPlugins(false);
    }

    render() {
        let alert = this.state.showAlert ? <ThemedAlert onClose={this.closeAlert} severity={this.state.alertSeverity}>{this.state.alertMsg}</ThemedAlert> : "";
        return (
            <Grid item xs={12}>
                <Collapse in={this.state.showAlert}>
                    {alert}
                </Collapse>
                <div>
                    <b>Active Plugins:</b>
                    <RefreshButton 
                    id="refreshButton" 
                    onClick={() => this.refreshPlugins(true)}
                    size="small"
                    >
                    <LoopIcon />
                    </RefreshButton>
                </div>
                <ThemedPaper>
                    <ThemedTreeView>
                        {
                            this.state.items.map((item, index) => 
                            (
                                <ThemedTreeItem key={`plugin-tree-item-${item}`} label={<Typography>{item}</Typography>} expandIcon={<ChevronRightIcon/>} collapseIcon={<ExpandMoreRoundedIcon/>} nodeId={index}>
                                    <PluginMetadata key={`plugin-item-${item}`} plugin_name={item} theme={this.props.theme}/>
                                </ThemedTreeItem>
                            ))
                        }
                    </ThemedTreeView>
                </ThemedPaper>
            </Grid>
        );
    }
    
}

export default PluginsList;